import {
	FULL_PAYMENT,
	DISPLAY_CAR,
	DEFAULT_ADDRESS,
	CHOOSE_CAR,
	CAR_DATA,
	AJAX_LOADING,
	RETURN_DATA,
	ALERT,
	ADDRESS_FLAG,
	DEFAULT_BRAND,
	SPARE_DATA,
	MESSAGE_FLAG
} from './mutations-types.js'


export default {

	[FULL_PAYMENT](context,obj){ //action mutations 
		context.commit(FULL_PAYMENT,obj);
	},

	[DISPLAY_CAR](context,obj){ //action mutations 
		context.commit(DISPLAY_CAR,obj);
	},

	[DEFAULT_ADDRESS](context,obj){ //action mutations  
		context.commit(DEFAULT_ADDRESS,obj);
	},

	[CHOOSE_CAR](context,obj){ //action mutations  
		context.commit(CHOOSE_CAR,obj);
	},

	[CAR_DATA](context,obj){ //action mutations  
		context.commit(CAR_DATA,obj);
	},

	[AJAX_LOADING](context,flag){ //action mutations  loading
		context.commit(AJAX_LOADING,flag);
	},


	[RETURN_DATA](context,obj){ //action mutations  
		context.commit(RETURN_DATA,obj);
	},

	[ALERT](context,obj){ //action mutations  
		context.commit(ALERT,obj);
	},

	[ADDRESS_FLAG](context,flag){ //
		context.commit(ADDRESS_FLAG,flag);
	},

	[DEFAULT_BRAND](context,flag){ //
		context.commit(DEFAULT_BRAND,flag);
	},

	[SPARE_DATA](context,flag){ //
		context.commit(SPARE_DATA,flag);
	},

	[MESSAGE_FLAG](context,flag){ //
		context.commit(MESSAGE_FLAG,flag);
	}
	
}



