import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex)


const state = {
	fullPaymentData:{}, //
	displayData:{}, //
	defaultAdress:{},
	chooseCar:false,
	carData:{
		carName:""
	},
	defaultBrand:"", //
	ajaxLoading:false, //ajaxLoading
	returnData:{}, //
	alert:{},
	addressFlag:"", //
	spareData:{}, //
	messageFlag:""//
}

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations
})


