import {
	FULL_PAYMENT,
	DISPLAY_CAR,
	DEFAULT_ADDRESS,
	CHOOSE_CAR,
	CAR_DATA,
	AJAX_LOADING,
	RETURN_DATA,
	ALERT,
	ADDRESS_FLAG,
	DEFAULT_BRAND,
	SPARE_DATA,
	MESSAGE_FLAG
} from './mutations-types.js'



export default {

	[FULL_PAYMENT](state,obj){ //
		state.fullPaymentData = obj;
	},

	[DISPLAY_CAR](state,obj){ //
		state.displayData = obj;
	},

	[DEFAULT_ADDRESS](state,obj){ //
		state.defaultAdress = obj;
	},

	[CHOOSE_CAR](state,flag){ //
		state.chooseCar = flag;
	},

	[CAR_DATA](state,obj){ //
		state.carData = obj;
	},

	[AJAX_LOADING](state,flag){ //
		state.ajaxLoading = flag;
	},

	[RETURN_DATA](state,obj){ //
		state.returnData = obj;
	},

	[ALERT](state,obj){ //
		state.alert = obj;
	},

	[ADDRESS_FLAG](state,flag){ //
		state.addressFlag = flag;
	},

	[DEFAULT_BRAND](state,flag){ //
		state.defaultBrand = flag;
	},

	[SPARE_DATA](state,obj){ //
		state.alert = obj;
	},

	[MESSAGE_FLAG](state,flag){ //
		state.messageFlag = flag;
	}

}



